<template>
  <div class="classification" :style="{ 'min-height': appHeight + 'px' }">
    <!--头部-->
    <header-top :kfshow.sync="kfshow">
      <img
        src="../../assets/image/icon-close2.png"
        width="28"
        height="28"
        slot="menu"
        @click="showMenu"
      >
      <router-link to="/home" slot="title" class="title-img">
        <img :src="logo?config.cdnbase+logo:''" alt="title" height="19">
      </router-link>
      <img
        width="28"
        height="28"
        src="../../assets/image/logo.png"
        alt="logo"
        class="logo"
        slot="home"
        @click="kfShow()"
      >
    </header-top>
    <div class="class-list" :style="{ 'min-height': appHeight - 60 + 'px' }">
      <!--一级类-->
      <div class="mdui-col-xs-4 side-bar1" :style="{ 'min-height': appHeight - 40 + 'px' }">
        <a class="mdui-col-xs-12" @click="showMenu">{{lang('热门')}}</a>
        <a
          href="javascript:;"
          v-for="(first,index) of firstList"
          :key="index"
          class="mdui-col-xs-12"
          @click="getClassInfo(first.id)"
          :class="activeId === first.id ? 'active' : ''"
        >{{first.name}}</a>
      </div>
      <!--二级类-->
      <div class="mdui-col-xs-8 side-bar2" :style="{ 'min-height': appHeight - 40 + 'px' }">
        <div
          class="tip"
          v-show="!menuList"
          :style="{ 'height': appHeight - 90 + 'px' }"
        >{{lang('请选择左侧类目')}}</div>
        <div class="mdui-col-xs-4 mdui-m-b-1" v-for="(classItem,index) in classList" :key="index">
          <router-link :to="`/class/${activeId}/menu/${classItem.id}`">
            <a href="javascript:;" @click="closeMenu(activeId,classItem.id)" class="second-class">
              <img :src="classItem.class_img?config.cdnbase+classItem.class_img:''" class="class-img">
              <span class="class-title">{{classItem.name}}</span>
            </a>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      kfshow: false,
      appHeight: window.innerHeight,
      classList: [],
      activeId: "",
      logo: "",
      menuList: false
    };
  },
  async created() {
    this.logo = this.$store.state.siteInfo.logo;
  },
  methods: {
    kfShow() {
      this.kfshow = !this.kfshow;
    },
    showMenu() {
      if (this.$router.history.current.path !== "/home") {
        this.$router.push({
          path: `/home`
        });
      }
      this.$emit("showMenu");
    },
    async getClassInfo(id) {
      this.activeId = id;
      this.classList = this.firstList.filter(item => {
        return item.id == id;
      })[0].subclass;
      this.menuList = true;
    },
    closeMenu() {
      this.showMenu();
    }
  },
  props: ["isShow", "firstList"],
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  }
};
</script>

<style scoped lang="stylus">
  .classification
    z-index 1000
    background-color: #fff
    .mdui-col-xs-4,.mdui-col-xs-8
      text-align center
      height:100%
      .class-title
        overflow: hidden
        text-overflow: ellipsis
        white-space: nowrap;
    .mdui-col-xs-4.mdui-m-b-1
      min-width:  min-content
    .header
      height: 40px
      line-height 40px
      /*padding-left: 1rem*/
      max-width: 750px;
      margin: 0 auto;
      border-bottom: 1px solid #EDEDED;
      overflow: hidden;
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1000;
      background-color: #fff;
      .title
        position: relative;
        height: 40px;
        line-height: 40px;
        color: #7B7B7B;
        text-align center
        padding: 0 8px;
        box-shadow: 0 1px 1px 0 rgba(237,237,237,1)
        background-color: #fff
      .fr
        margin-top: .8rem
        margin-right: 1rem
    .class-list
      padding-top: 40px
      position relative
      .side-bar1
        position: absolute;
        left: 0;
        top: 40px;
        bottom: 0;
        width: 110px;
        z-index: 2;
        overflow-y auto
        padding-bottom 50px
      .side-bar2
        position: absolute;
        left: 110px;
        right: 0;
        top: 40px;
        bottom: 0;
        z-index: 2;
        width: auto;
        overflow-y auto
      &::after
        display: block
        content: ''
        clear: both
      &>.mdui-col-xs-4
        border-right: 1px solid #F2F2F2
        .mdui-col-xs-12
          display: block
          background: #fff;
          margin: 6px auto;
          box-sizing: border-box;
          width: 90%;
          height: 28px;
          line-height 28px
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap
          border-radius: 18px;
          &.active
            background: #B4272D;
            color: #FFFFFF
      .mdui-col-xs-8
        .tip
          display: flex
          justify-content center
          align-items center
          color: #777
  @media (min-width: 768px){
    .class-img {
      width: 132px
      height: 132px
      display block
      margin 0 auto 5px
    }
  }
  @media (max-width: 414px){
    .class-img {
      width: 54px
      height: 54px
      display block
      margin 0 auto 5px
    }
  }
  .title-img {
    position: absolute;
    left: 45px;
    top:5px
    margin 0 5px
  }
  .second-class{
    margin-top: 15px;
    display: inline-block;
  }
</style>
