<template>
	<div id="app" :style="{'min-height':appHeight + 'px'}">
		<router-view></router-view>
	</div>
</template>
<script>
import {site} from './lib/goods'
import {mapMutations, mapState} from 'vuex'
import {advertisement} from './lib/util'
  export default {
    data() {
      return {
        appHeight: window.innerHeight
      }
    },
    async created(){
      const data = await site.get_site()
      if (!data.Error) {
        this.setSite(data.site);
        this.setSiteClass(data.site_class);
        this.setLanguage(data.lang);
        this.setConfig(data.config)
      }
    },
    mounted(){
      let favicon = this.siteInfo.favicon
      var head = document.getElementsByTagName('head')[0],
      linkTag = document.createElement('link');
      linkTag.setAttribute('rel', 'icon');
      linkTag.setAttribute('href', this.cdnbase+favicon);
      head.appendChild(linkTag);
      if (this.siteInfo && this.siteInfo.adcode) {
		const siteInfo = this.siteInfo;
		advertisement(siteInfo.adcode, siteInfo.languagecode, siteInfo.othercode)
      }
    },
    methods: {
      ...mapMutations(['setSite', 'setSiteClass', 'setLanguage', 'setConfig']),
    },
    computed: {
      ...mapState(['language','siteInfo']),
      lang () {
        return function (str) {
          return this.language ? this.language[str] : ''
        }
      }
    },
    watch: {
      "$route" (to) {
        if (to.name === 'home') document.title = this.lang('首页')
        if (to.name === 'class') document.title = this.lang('分类')
        if (to.name === 'shoppingCart') document.title = this.lang('购物车')
        if (to.name === 'cartPay') document.title = this.lang('填写付款资料')
        if (to.name === 'user') document.title = this.lang('我的')
      }
    }
  }
</script>
<style lang="stylus">
	@import "assets/css/base.css"
	body, html
		background-color: #f4f4f4 !important;
		a
			text-decoration: none
			color: #000

	.mdui-bottom-nav
		label
			font-size 12px
			margin-top 3px !important

		a.mdui-bottom-nav-active
			color #B4272D !important
</style>
