export const advertisement = function (code, languagecode, othercode) {
	if (code.length===0) return
	var hm = document.createElement('script')
	var nosc = document.createElement('noscript')
	var div = document.createElement('div')
	hm.innerHTML = `!function (f, b, e, v, n, t, s) {
					if (f.fbq) return;
					n = f.fbq = function () {
							n.callMethod ? n.callMethod.apply(n, arguments) : n.queue.push(arguments);
					};
					if (!f._fbq) f._fbq = n;
					n.push = n;
					n.loaded = !0;
					n.version = '2.0';
					n.queue = [];
					t = b.createElement(e);
					t.async = !0;
					t.src = v;
					s = b.getElementsByTagName(e)[0];
					s.parentNode.insertBefore(t, s)
			}(window, document, 'script', 'https://connect.facebook.net/${languagecode}/fbevents.js');`
	for (let item1 of code) {
		nosc.innerHTML += `<img height="1" width="1" style="display:none" src="https://www.facebook.com/tr?id=${item1.fbcode}&ev=PageView&noscript=1"/>`
	}
	document.body.appendChild(hm)
	for (let item2 of code) {
		var h2 = document.createElement('script')
		h2.innerHTML = `fbq('init', ${item2.fbcode});fbq('track', 'PageView');`
		document.body.appendChild(h2)
	}
	document.body.appendChild(nosc)
	for (let item3 of othercode) {
		div.innerHTML += item3.code
		div.width = 0
	}
	document.body.appendChild(div)
}

export const creditPlgin = function ({id, ele, src}) {
    document.getElementById(id) && document.getElementById(id).remove()
    var element = document.createElement(ele)
    var app = document.getElementById('app')
    element.id = id
    element.src = src
    app.parentElement.insertBefore(element,app)
}

export const removeCredit = function (id) {
    document.getElementById(id) && document.getElementById(id).remove()
}
