import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'
import Toast from 'vue2-toast'
import 'vue2-toast/lib/toast.css'
import footerNav from './components/commen/footer.vue'
import headerTop from './components/commen/header.vue'
import classList from './components/commen/classList.vue'
import minFooter from './components/commen/minFooter'
import VueLazyload from 'vue-lazyload'
import VueClipboard from 'vue-clipboard2'

Vue.use(VueClipboard)
Vue.use(VueLazyload, {
  preLoad: 1.0,
  error: require('../src/assets/image/load.png'),
  loading: require('../src/assets/image/load.png')
})
Vue.use(Toast, {
  defaultType: 'center',
  duration: 2000,
  wordWrap: true,
  width: '150px'
})
// 整个模块注册
// Vue.prototype.cdnbase = 'http://cdn.seezt.cc/uploadimages/'
Vue.prototype.$Api_Url = process.env.VUE_APP_API_URL
Vue.prototype.$Order_Url = process.env.VUE_APP_ORDER_URL

// 全局组件注册
const nav = {
  install: function (Vue) {
    Vue.component('footerNav', footerNav)
    Vue.component('headerTop', headerTop)
    Vue.component('classList', classList)
    Vue.component('minFooter', minFooter)
  }
}
Vue.use(nav)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
