
export default [
	{
		path: '/',
		name: 'home',
		component: () => import('../views/home/Home.vue')
	},
	{
		path: '/classList',
		name: 'classList',
		component: () => import(/* webpackChunkName: "classList" */'../components/commen/classList.vue')
	},
	{
		path: '/home/:id',
		component: () => import('../views/home/Home.vue')
	},
	{
		path: '/home',
		component: () => import('../views/home/Home.vue'),
		redirect: '/'
	},
	{
		path: '/detail/:typeid/:id/',
		name: 'detail',
		component: () => import(/* webpackChunkName: "detail" */'../views/detail/Detail.vue')
	},
	{
		path: '/class',
		name: 'class',
		component: () => import(/* webpackChunkName: "class" */'../views/classification/Classification.vue')
	},
	{
		path: '/shoppingcart',
		name: 'shoppingCart',
		component: () => import(/* webpackChunkName: "shoppingcart" */'../views/shoppingCart/ShoppingCart.vue')
	},
	{
		path: '/user',
		name: 'user',
		component: () => import(/* webpackChunkName: "user" */'../views/user/User.vue')
	},
	{
		path: '/class/:id/menu/:mid',
		name: 'menu',
		component: () => import(/* webpackChunkName: "class" */'../views/menu/Menu.vue')
	},
	{
		path: '/orderconfirm',
		name: 'cartPay',
		component: () => import(/* webpackChunkName: "orderconfirm" */'../views/shoppingCart/OrderConfirm.vue')
	},
	{
		path: '/finishorder',
		name: 'finish',
		component: () => import(/* webpackChunkName: "finishorder" */'../views/shoppingCart/FinishOrder.vue')
	},
	{
		path: '/success',
		name: 'success',
		component: () => import(/* webpackChunkName: "finishorder" */'../views/shoppingCart/FinishOrder.vue')
	},
	{
		path: '/About/:id',
		name: 'about',
		component: () => import(/* webpackChunkName: "about" */'../views/about/About.vue')
	},
  {
    path: '/404',
    name: 'error',
    component: () => import(/* webpackChunkName: "about" */'../views/error/404.vue')
  },
  {
    path: '*',
    name: 'error',
    component: () => import(/* webpackChunkName: "about" */'../views/error/404.vue')
  }
]
