import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		orderId: '', //订单id
    orderInfo:'',
		imglist: [],
		classInfo: [],
		language: {},
		siteInfo: {},
    config: '',
    pay_receipt_url: ''
	},
	mutations: {
		setSite(state, data) {
			state.siteInfo = data
		},
		setLanguage(state, data) {
			state.language = data
		},
		setSiteClass(state, data) {
			state.classInfo = data
		},
    setConfig(state, data){
      state.config = data
    },
    setOrder(state, data){
      state.orderInfo = data
    },
		setImgs(state, data){
			state.imglist = data
		}
	},
	getters: {
		getSite: (state) => {
			return state.siteInfo;
		}
	},
	plugins: [createPersistedState({
		storage: window.localStorage,
    reducer(val) {
      return {
        orderInfo: val.orderInfo,
        classInfo: val.classInfo,
        language: val.language,
        siteInfo: val.siteInfo,
        config: val.config,
				imglist: val.imglist
      }
    }
	})],
})
