import http from './http'

export const home = {
	// 获取首页信息
	get_home_goods: async () => { // 首页商品楼层信息
		let url = `home`
		return http.get(url)
	},
	get_home_productAll: async ({ id1, id2 }) => {
		let url
		if (id2 === undefined) {
			url = `class/${id1}`
		} else {
			url = `class/${id1}/${id2}`
		}
		return http.get(url)
	}
}
export const classPage = {
	get_class_goodAB: async () => { // 一级分类
		let url = `type/get`
		return http.get(url)
	},
	get_class_product: async ({ id1, id2 }) => {
		let url
		if (id2 === undefined) {
			url = `class/${id1}`
		} else {
			url = `class/${id1}/${id2}`
		}
		return http.get(url)
	}
}

export const goods = {
	// 获取商品信息
	get_detail_info: async (vue) => {
		let params = vue.$route.params
		let url
		url = 'detail/' + params.typeid
		url += '/' + params.id
		return http.get(url)
	},

	// get_detail_info: async (vue) => {
	// 	let params = vue.$route.params
	// 	let url = 'detail'
	// 	return http.get(url + "/" + ,{did:params.typeid,id:params.id})
	// },
	// 获取商品规格
	get_detail_spec: async (id) => {
		let url = 'spec/all/?id=' + id
		return http.get(url)
	},
	//更新浏览量
	update_page_viewsc: async (id) => {
		let url = `viewcount/${id}`
		return http.get(url)
	},
	// 获取商品指定规格
	get_detail_idspec: async (id) => {
		let url = 'spec/getbyid/?id=' + id
		return http.get(url)
	},
	// 商品信息处理
	format: function (goods) {
		let goods_list = {}
		let name_key_list = []
		let name_list = []
		let option1_key_list = []
		let option1_list = []
		let option2_key_list = []
		let option2_list = []
		if (goods) {
			for (let k in goods) {
				let spec = goods[k]
				if (spec.name !== '' && spec.name !== null) {
					if (name_key_list.indexOf(spec.name) < 0) {
						name_key_list.push(spec.name)
						name_list.push(spec)
					}
				}
				if (spec.option1 !== '' && spec.option1 !== null) {
					if (option1_key_list.indexOf(spec.option1) < 0) {
						option1_key_list.push(spec.option1)
						spec.father = name_list[name_key_list.indexOf(spec.name)] ? name_list[name_key_list.indexOf(spec.name)].id : ''
						option1_list.push(spec)
					}
				}
				if (spec.option2 !== '' && spec.option2 !== null) {
					if (option2_key_list.indexOf(spec.option2) < 0) {
						option2_key_list.push(spec.option2)
						spec.father = option1_list[option1_key_list.indexOf(spec.option1)] ? option1_list[option1_key_list.indexOf(spec.option1)].id : ''
						option2_list.push(spec)
					}
				}
			}
			return goods_list.specs = {
				list: goods,
				name_key_list: name_key_list,
				name_list: name_list,
				option1_key_list: option1_key_list,
				option1_list: option1_list,
				option2_key_list: option2_key_list,
				option2_list: option2_list
			}
		}
	}
}
