export const order = {
	submit_order: async (goodsinfo, cartinfo, clientinfo, referrer, siteurl) => {
		let url = `createOrder`
		return http.orderpost(url, {
			goodsinfo: goodsinfo,
			cartinfo: cartinfo,
			clientinfo: clientinfo,
			referrer: referrer,
			siteurl: siteurl
		})
	},
  submitOrderPaypal: async (data) => {
    let url = `createOrder`
    return http.orderpost(url, data)
  }
}

